<template>
	<view>
		<u-modal v-model="sqMode" :mask-close-able="true" title="授权提示" :show-confirm-button="false" :show-cancel-button="true"
		 cancel-text="取消授权">
			<!-- <open-data type="userAvatarUrl"></open-data>
			<open-data type="userNickName"></open-data> -->
			<button shape="circle" class="login-btn-style" open-type="getUserInfo" @getuserinfo="bindGetUserInfo" type="primary">授权登陆</button>
		</u-modal>
		<view class="login-img-box">
			<view class="login-logo-box">
				<view class="login-logo">
					<!-- <image class="login-logo-img" src="../../static/ent/login2.png"></image> -->
					<image class="login-logo-img" src="http://www.ycbx.xyz:8088/login2.png"></image>
				</view>
			</view>
			<!-- <image class="login-img" src="../../static/ent/login.png"></image> -->
			<image class="login-img" src="http://www.ycbx.xyz:8088/login.png"></image>
		</view>
		<view class="login-form-box">
			<p class="login-form-title">欢迎登录！</p>
			<view style="padding: 0 10%;">
				<u-form :model="form" :rules="rules" ref="uForm">
					<u-form-item label="工号" prop="userName">
						<u-input v-model="form.userName" />
					</u-form-item>
					<u-form-item label="密码" prop="userPassword">
						<u-input v-model="form.userPassword" type="password" />
					</u-form-item>
					<!-- <view class="login-form-row">
						<view class="login-form-ico">
							<u-icon name="account-fill" color="#2979ff" size="36"></u-icon>
						</view>
						<view class="login-form-input">
							<u-input :border="border" placeholder="" v-model="name" type="text"></u-input>
						</view>
					</view>
					<view class="login-form-row">
						<view class="login-form-ico">
							<u-icon name="lock-fill" color="#2979ff" size="36"></u-icon>
						</view>
						<view class="login-form-input">
							<u-input :password-icon="true" :border="border" type="password" v-model="password" placeholder=""></u-input>
						</view>
					</view> -->
				</u-form>
				<view style="margin-top: 20%;">
					<!-- <u-button shape="circle" class="login-btn-style" open-type="getUserInfo" @getuserinfo="bindGetUserInfo" type="primary">授权登陆</u-button> -->
					<u-button shape="circle" class="login-btn-style" type="primary" @click="clearLogin">暂不登录</u-button>
					<u-button shape="circle" class="login-btn-style" type="primary"  open-type="getUserInfo" @getuserinfo="loginBtn">登录绑定</u-button>
				</view>
			</view>
		</view>
		<view class="copyright">
			<p>©KMYG技术支持</p>
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				// 授权
				sqMode: false,
				form: {
                    userName: '',
                    userPassword: '',
                    code: '',
                    avatarUrl:'',
                    nickName:'',

				},
				rules: {
                    userName: [{
						required: true,
						message: '工号不能为空',
						trigger: ['change', 'blur']
					}],
                    userPassword: [{
						required: true,
						message: '密码不能为空',
						trigger: ['change', 'blur']
					}]
				}
			}
		},
		onLoad() {
			// this.checkPer()
		},
		methods: {
			// 获取授权信息
			bindGetUserInfo(e) {
				var that = this
				wx.getSetting({
					success(res) {
						if (res.authSetting['scope.userInfo']) {
							wx.getUserInfo({
								success: function(res) {
									// console.log(res.userInfo)
									uni.setStorage({
										key: 'wxUserInfo',
										data: res.userInfo
									})
									wx.login({
										//获取code
										success: function(res) {
											var code = res.code; //返回code,根据code获取openId和sessionkey
											var requestBody = {
												userName: that.form.name,
												passWord: that.form.pwd,
												code: code
											}
											that.$u.post('/wx/wxBindByAfter', requestBody).then(res => {
												if (res.code === 0) {
													// 存储相关信息
													uni.setStorage({
														key: 'pcUserInfo',
														data: res.data
													})
													uni.switchTab({
														url: '/pages/center/index'
													});
												} else {
													uni.showToast({
														title: res.msg
													})
												}
												console.log('aa' + JSON.stringify(res))
											})

										}
									})
								}
							})
						}
					}
				})
			},
			//暂不登录
            clearLogin(){
                this.$u.route({
                    type: 'switchTab',
                    url: 'pages/center/index',
                    params: {
                        name: 'lisa'
                    }
                })
			},
			// 登录
			loginBtn(e) {
			    //手动登录，先获取授权
                // 获取用户信息 可以放在首页用，判断用户是否已经受过权了
               /* uni.getSetting({
                        success(res) {
							console.log("已授权")
							console.log(res)
                            //if (res.authSetting['scope.userInfo']) {
                                //
                                uni.getUserInfo({
                                    success: function(resUserInfo) {
                                        uni.setStorageSync('userInfo', resUserInfo.userInfo)
                                        console.log("已授权")
                                        console.log(resUserInfo)
									}
                                })
							//}
						}
                }) */
                var that = this
                //若是首次会确认授权
                uni.getUserInfo({
                    provider: 'weixin',
                    success: (infoRes) => {
                    uni.setStorageSync('userInfo', infoRes.userInfo)
               			 console.log(infoRes)
						// 封装基本信息
						that.form.avatarUrl = infoRes.userInfo.avatarUrl
						that.form.nickName =infoRes.userInfo.nickName

						// 验证
						this.$refs.uForm.validate(valid => {
							if (valid) {
							    //登录操作
								uni.login({
									provider: 'weixin',
									success: (eLogin) => {
										that.form.code = eLogin.code
										uni.setStorageSync('code', eLogin.code)
										that.$u.post('/wxBind',this.form).then(bindRes => {
										   that.$store.commit('token', bindRes.data.result.token)
										   that.$store.commit('setUser', bindRes.data.result)
										  
										   setTimeout((_)=>{
											   console.log("留有10毫秒的时间去自动设置http的请求头的token,以保证接下来获取权限的请求带有的token不为空")
										   },10)
										   //获取权限
										    that.$u.get('/permission/findByCurrentUser').then(loginRes => {
										       			 console.log("获取权限成功")
										               that.$store.commit('setAccess', loginRes.data.data.permissions)
										               uni.showToast({title: "获取权限成功!", icon: "none"});
										              
										              that.goToOther()
										           }).catch(res=>{
										            
										               uni.showToast({title: "获取权限失败!", icon: "none"});
										  that.goToOther()
										           });
											uni.showToast({title: "登录成功!", icon: "none"});						
										}).catch(res=>{
											uni.showToast({title: res.data.msg, icon: "none"});
										});
		
									}
								})
						
							}else{
                                uni.showToast({title: "请填写正确", icon: "none"})
							}
						});


					 },
					fail: (infoRes) => {
						uni.showToast({title: "获取用户信息失败,请确认授权！", icon: "none"});
					}
				});

			},
			//回到个人中心
			goToOther(){
				this.$u.route({
				    type: 'switchTab',
					url: 'pages/center/index',
					params: {
						name: 'lisa'
					}
				})
			},
		},
		
		onReady() {
            /*var that = this
                this.$refs.uForm.validate(valid => {
                    if (valid) {
                        var requestBody = {
                            userName: that.form.name,
                            passWord: that.form.pwd,
                            code: ''
                        }
                        this.$u.post('/wx/wxBindByBefore', requestBody).then(res => {
                            if (res.code === 0) {
                                that.sqMode = true
                                console.log('验证通过');
                            }else{
                                uni.showToast({
                                    title: res.msg
                                })
                            }
                            // console.log(res)
                        }).cache
                    } else {
                        console.log('验证失败');
                    }
                });*/
			this.$refs.uForm.setRules(this.rules);
		},
	}
</script>

<style>
	.login-logo .login-logo-img {
		width: 70%;
		height: 70%;
		margin: 15% 15%;
		margin-top: 20%;
	}

	.copyright {
		position: fixed;
		width: 60%;
		margin: 0 20%;
		bottom: 2%;
		color: #2b85e4;
		font-size: 28rpx;
		text-align: center;
	}

	.login-btn-style {
		/* margin-top:25%; */
		box-shadow: 0rpx 6rpx 15rpx -3rpx rgba(6, 106, 191, 0.8);
	}

	.login-form-title {
		text-align: center;
		font-size: 50rpx;
		color: #2979ff;
		padding: 40rpx 0;
		font-weight: 900;
	}

	.login-form-row {
		width: 100%;
		margin-bottom: 30rpx;
		border-bottom: solid 1px #a0cfff;
	}

	.login-form-input {
		/* width: 85%; */
	}

	.login-form-ico {
		float: left;
		width: 15%;
		text-align: center;
		padding-top: 15rpx;
	}

	.login-ico-left .u-form-item--left {
		float: left !important;
	}

	.login-form-box {
		width: 90%;
		height: 600rpx;
		margin: 0 5%;
		background-color: #FFFFFF;
		box-shadow: 1px 4px 36px -12px rgba(6, 106, 191, 0.8);
		border-radius: 25rpx;
		margin-top: -20%;
		z-index: 9999 !important;
		position: absolute;
	}

	.login-logo-box {
		width: 180rpx;
		height: 180rpx;
		margin-left: auto;
		margin-right: auto;
		padding-top: 20%;
	}

	.login-logo {
		position: absolute;
		width: 180rpx;
		height: 180rpx;
		border-radius: 180rpx;
		background-color: rgba(255, 255, 255, 0.9);
		z-index: 999;
		margin-left: auto;
		margin-right: auto;
		border: solid 5px rgba(35, 146, 241, 0.3);
	}

	.login-img {
		width: 100% !important;
		margin-top: -150rpx;
	}

	.login-img-box {
		width: 100% !important;
		position: relative;
	}
</style>
